import { createRouter, createWebHistory} from 'vue-router';

import HomePage from './pages/HomePage.vue';


import Meurs from './pages/Meurs.vue';
import Angelz from './pages/Angelz.vue';
import FouMetro from './pages/FouMetro.vue';
import EvenPunksRead from './pages/EvenPunksRead.vue';
import Witchy from './pages/Witchy.vue';
import ParisToSaigon from './pages/ParisToSaigon.vue';
import DanseDansLeNoir from './pages/DanseDansLeNoir.vue';
import Manifique from './pages/Manifique.vue';
import FashionWeek from './pages/FashionWeek.vue';

import KingdomHearts from './pages/KingdomHearts.vue';
import Doves from './pages/Doves.vue';

import Margin from './pages/Margin.vue';
import Tipsy from './pages/Tipsy.vue';
import Bollore from './pages/Bollore.vue';

const routes = [
    {path:'/', name:'Home', component: HomePage},

    {path:'/meurs', name:'Meurs', component: Meurs},
    {path:'/angelz', name:'Angelz', component: Angelz},
    {path:'/fou-metro', name:'FouMetro', component: FouMetro},
    {path:'/even-punks-read', name:'EvenPunksRead', component: EvenPunksRead},
    {path:'/witchy', name:'Witchy', component: Witchy},
    {path:'/paris-to-saigon', name:'ParisToSaigon', component: ParisToSaigon},
    {path:'/danse-dans-le-noir', name:'DanseDansLeNoir', component: DanseDansLeNoir},
    {path:'/manifique', name:'Manifique', component: Manifique},
    {path:'/fashion-week', name:'FashionWeek', component: FashionWeek},

    {path:'/kingdom-hearts', name:'KingdomHearts', component: KingdomHearts},
    {path:'/doves', name:'Doves', component: Doves},

    {path:'/margin', name:'Margin', component: Margin},
    {path:'/tipsy', name:'Tipsy', component: Tipsy},
    {path:'/bollore', name:'Bollore', component: Bollore},
];

const router = createRouter({
    history: createWebHistory(),
    routes
});


export default router;