<template>
    <div id="seriesImages">
        <img 
            v-for="(image, index) in images" 
            :key="index" 
            :src="image" 
            :alt="'Image ' + (index + 1)"
            @click="openModal(image)"
        >
    </div>

    <!-- Fenêtre modale pour afficher l'image en grand -->
    <div v-if="modalVisible" class="modal" @click="closeModal">
        <img :src="modalImage" class="modal-content">
    </div>
</template>

<script>
export default {
    data() {
        return {
            images: [
                "/images/fashion-week/1.jpg",
                "/images/fashion-week/2.jpg",
                "/images/fashion-week/3.jpg",
                "/images/fashion-week/4.jpg",
                "/images/fashion-week/5.jpg",
                "/images/fashion-week/6.jpg",
                "/images/fashion-week/7.jpg",
                "/images/fashion-week/8.jpg",
                "/images/fashion-week/9.jpg",
                "/images/fashion-week/10.jpg"
            ],
            modalVisible: false,
            modalImage: ""
        };
    },
    methods: {
        openModal(image) {
            this.modalImage = image;
            this.modalVisible = true;
        },
        closeModal() {
            this.modalVisible = false;
        }
    }
};
</script>

<style scoped>
#seriesImages {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; /* Centrage horizontal */
    align-items: center; /* Centrage vertical */
    height: 100vh; /* Utilisation de toute la hauteur de l'écran */
    gap: 16px;
}

#seriesImages img {
    border-radius: 8px;
    object-fit: cover;
    width: 200px; /* Taille fixe pour un affichage uniforme */
    height: auto;
    cursor: pointer;
    transition: transform 0.3s ease;
}

#seriesImages img:hover {
    transform: scale(1.05);
}

/* Fenêtre modale */
.modal {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    z-index: 1000;
}

/* Image en grand */
.modal-content {
    max-width: 90%;
    max-height: 90%;
    border-radius: 8px;
}
</style>
